import React from "react"
import ReactMarkdown from "react-markdown"

import Layout from "../components/layout"
import CourseLayout from "../components/courseLayout"

import SEO from "../components/seo"

import { CardDeck, Card, Row, Col, Container } from "react-bootstrap" 



const CoursePage = ({data}) => {

	const courseInfo = `## ML101: Introduction to Machine Learning \n \
	Autumn 2019 

	Course intro slides are avialable somewhere

![alt text](https://i.gifer.com/B4yq.gif)


| Factors impacting final grade | Factor's impact factor |
| --------- | ------- |
| Homeworks    | 0.495 |
| Class work | 0.2 |
| Zachet | 0.3 |
| Past merits       | 0.005 |
| Rich parents       | 0.000 |

	`;

	return (
	  <Layout>
	  	<CourseLayout>
	    	<SEO title="Course ML101" />

			<ReactMarkdown source={courseInfo} />
		</CourseLayout>
	  </Layout>
	)
}


export default CoursePage
